<template>
    <div class="dialog-container">
        <div class="dialog-bg"></div>
        <div class="mb-service-dialog-box" @click="errorMessage = ''">
            <a @click="closeDialog()"></a>
            <span class="title">{{isClickShare ? $t('MBServiceDialog.Share-Service') : isClickBindEmail ? $t('MBServiceDialog.Bound-Email') : $t('MBServiceDialog.Reset-Service')}}</span>
            <div class="share-box" v-if="isClickShare">
                <!--<a @click="isClickShare = false"></a>-->
                <div class="card-box-bg panel-address-box panel-address-box-margin">
                    <span class="share-first-row">{{ $t('MBServiceDialog.Manage-Address') }}</span>
                    <span class="share-sec-row">{{ $t('MBServiceDialog.Keep-Secret') }}</span>
                    <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                        <template slot="title">
                            <span>{{ $t('MBServiceDialog.Copied') }}</span>
                        </template>
                        <span class="share-third-row" v-clipboard:copy="selectedItem.dashboardUrl">{{ $t('MBServiceDialog.Copy-Address') }}</span>
                    </a-tooltip>
                </div>
                <div class="card-box-bg  panel-address-box">
                    <span class="share-first-row">{{ $t('MBServiceDialog.Use-Address') }}</span>
                    <span class="share-sec-row">{{ $t('MBServiceDialog.Share-With-Friend') }}</span>
                    <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                        <template slot="title">
                            <span>{{ $t('MBServiceDialog.Copied2') }}</span>
                        </template>
                        <span class="share-third-row" v-clipboard:copy="selectedItem.usageDashboardUrl">{{ $t('MBServiceDialog.Copy-Address2') }}</span>
                    </a-tooltip>
                </div>
            </div>
            <div class="share-box" v-if="isClickBindEmail">
                <div class="card-box-bg  panel-address-box">
                    <span class="email-first-row">{{ $t('MBServiceDialog.Bound-Email2') }}</span>
                    <span class="email-sec-row">{{ $t('MBServiceDialog.Find-Back-Service') }}</span>
                    <div class="email-third-row">
                        <a-input :placeholder="$t('MBServiceDialog.Please-Enter-Email')" :class="{'email-input-error':emailErrorMsg === $t('Please-Valid-Email')}" v-model="selectedItem.email" @change="emailChange" :disabled="selectedItem.emailStatus === 'Binded'"></a-input>
                        <span class="email-center" v-if="isStartBindEmail && !isCountTimeEnd">{{secondNum}}(s)</span>
                        <span class="email-right" @click="startBindEmail()" v-if="isStartBindEmail && isCountTimeEnd">{{ $t('MBServiceDialog.Resend') }}</span>
                        <span class="email-right" @click="clickUnbindEmail" v-if="selectedItem.emailStatus === 'Binded'">{{ $t('MBServiceDialog.Unbound') }}</span>
                        <span class="email-right" @click="startBindEmail()" v-if="selectedItem.emailStatus !== 'Binded' && !isStartBindEmail">{{ $t('MBServiceDialog.Bound') }}</span>
                        <span class="email-right" @click="revokeEmail" v-if="selectedItem.emailStatus !== 'Binded' && isStartBindEmail">{{ $t('MBServiceDialog.Revoke') }}</span>
                    </div>
                    <span class="email-four-row" :class="{'error-tips': emailErrorMsg !== '验证邮件已发送'}">{{emailErrorMsg}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {AU_EMAIL_STATUS, AU_HMAC, AU_UUID} from "@/requestServer";
    export default {
        name: "AccessPointDialog",
        data(){
          return {
              isClickShare: false,
              isClickBindEmail: false,
              isStartBindEmail: false,
              emailErrorMsg: '',
              emailReg: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
              secondNum: 30,
          }
        },
        props: {
            clickShare: {},
            clickBindEmail: {},
            selectedItem:{},
        },
        watch: {
            clickShare: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.isClickShare = value;
                    }
                }
            },
            clickBindEmail: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.isClickBindEmail = value;
                    }
                }
            },
            selectedItem: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        console.log('value', value);
                    }
                }
            },
        },
        methods:{
            emailChange() {
                if (this.selectedItem.email) {
                    this.emailErrorMsg = ''
                }
            },
            startBindEmail() {
                if (this.selectedItem.email) {
                    if (this.emailReg.test(this.selectedItem.email)) {
                        let that = this;
                        this.$request.bindEmail(this.selectedItem.email, function (data) {
                            if (data.status === 200) {
                                that.isStartBindEmail = true;
                                that.startCountTime();
                                that.emailErrorMsg = this.$t('Verify-Email-Send');
                            } else {
                                that.emailErrorMsg = this.$t('Send-Error');
                            }
                        });
                    } else {
                        this.emailErrorMsg = this.$t('Please-Valid-Email');
                    }
                } else {
                    this.emailErrorMsg = this.$t('Please-Valid-Email');
                }
            },
            revokeEmail() {
                if (this.checkTimer > 0) {
                    window.clearInterval(this.checkTimer);
                    this.checkTimer = 0;
                    this.totalCheckCount = 0;
                }
                this.isStartBindEmail = false;
                this.selectedItem.email = '';
                this.emailErrorMsg = '';
            },
            clickUnbindEmail() {
                let that = this;
                this.$request.unBindEmail(this.selectedItem.email, function (data) {
                    if (data.status === 200) {
                        that.getServiceInfo();
                    }
                });
            },
            startCountTime() {
                this.isCountTimeEnd = false;
                let test = setInterval(() => {
                    this.secondNum--;
                    if (this.secondNum === 0 || this.selectedItem.emailStatus === 'Binded' || !this.isStartBindEmail) {
                        window.clearInterval(test);
                        this.secondNum = 30;
                        this.isCountTimeEnd = true;
                        if (this.selectedItem.emailStatus === 'Binded') {
                            this.isStartBindEmail = false;
                        }
                    } else if (this.secondNum === 10) {
                        this.emailErrorMsg = '';
                    }
                    if (this.secondNum === 25) {
                        if (this.checkTimer === 0) {
                            this.checkTimer = setInterval(() => {
                                this.totalCheckCount++;
                                if (!this.isStartBindEmail || this.selectedItem.emailStatus === 'Binded' || this.totalCheckCount === 15 || this.checkTimer === 0) {
                                    window.clearInterval(this.checkTimer);
                                    this.checkTimer = 0;
                                    this.totalCheckCount = 0;
                                } else {
                                    this.getServiceInfo();
                                }
                            }, 4000);
                        }
                    }
                }, 1000);
            },
            closeDialog(){
                this.errorMessage = '';
                this.$emit('closeDialog',false,'')
            }
        }
    }
</script>

