                               :class="{'item-open-icon-close': item.isShow, 'item-open-icon-open': !item.isShow}"></i>
                        </div>
                        <div class="content-list" :class="{'content-list-open': item.isShow}">
                            <div class="client-name-content" v-for="(subItem, articleIndex) in item.articleList"
