                                <i class="client-icon" :class="{
                                      'v2rayn-icon': subItem.name.toLowerCase().indexOf('v2rayn') > -1 && subItem.name.toLowerCase().indexOf('v2rayng') <= -1,
                                      'v2rayng-icon': subItem.name.toLowerCase().indexOf('v2rayng') > -1,
                                      'quantumult-icon': subItem.name.toLowerCase().indexOf('quantumult') > -1 && subItem.name.toLowerCase().indexOf('quantumult x') <= -1,
                                      'quantumultx-icon': subItem.name.toLowerCase().indexOf('quantumult x') > -1,
                                      'shadowrocket-icon': subItem.name.toLowerCase().indexOf('shadowrocket') > -1,
                                      'clash-icon': subItem.name.toLowerCase().indexOf('clash') > -1,
