                                      'whaleblue-icon': subItem.name.toLowerCase().indexOf('whaleblue') > -1,
                                      'gost-icon': subItem.name.toLowerCase().indexOf('gost') > -1,
                                      'lede-icon': subItem.name.toLowerCase().indexOf('lede') > -1,
                                      'merlin-icon': subItem.name.toLowerCase().indexOf('merlin') > -1,}"></i>
                                <p class="client-name">{{subItem.name}}</p>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
            <div v-if="!isArticleLoading && !isError" class="tutorial-article">
                <div class="articles-detail">
                    <SimpleArticle
                            :article="selectedArticle.url"
                            :isSimpleArticle="true"
                            v-on:detailedTutorialId="detailedTutorialId">
                    </SimpleArticle>
                </div>
            </div>
            <div v-if="!isArticleLoading && !isError" class="tutorial-qr">
                <div v-if="subscribeUrl.length > 0" class="qr-container"
                     :class="{'tutorial-qr-hide': (selectedArticle.name.toLowerCase().indexOf('whaleblue') > -1 || selectedArticle.name.toLowerCase().indexOf('jetstream') > -1)}">
                    <p class="qr-title" v-if="selectedArticle.templateList.length === 1">{{$t('ClientTutorial.Subscription-Address')}}</p>
                    <a-select class="qr-title" style="width: 220px;height: 36px;" :placeholder="$t('ClientTutorial.Please-Select-Placeholder')"
                              showSearch
                              v-model="selectedTemplateId"
                              @change="templateChange"
                              v-if="selectedArticle.templateList.length > 1">
                        <a-select-option :value="item.id" v-for="(item,index) in selectedArticle.templateList"
                                         :key="index">
                            {{'GFW List'}}
                        </a-select-option>
                    </a-select>
                    <div class="sub-box">
                        <div class="hide-box" v-if="serviceStatus !== 'Active'">购买服务后可见</div>
                        <div class="qr-url-content">
                            <span v-if="!isAuApp" class="qr-url-text notranslate">{{subscribeUrl}}</span>
                            <a-tooltip v-if="!isAuApp" placement="top" trigger="click" :visible="showTips">
                                <template slot="title">
                                    <span>{{$t('ClientTutorial.Copied')}}</span>
                                </template>
                                <span class="url-copy-btn" v-if="!isAuApp" @click="copySubUrl(selectedArticle.name)"
                                      @mouseleave="showTips = false">{{$t('ClientTutorial.Copy-Subscription-Address')}}</span>
                            </a-tooltip>
                            <div class="i-qr-code" id="qrcode" ref="qrcode" v-if="isAuApp"></div>
                            <span class="auapp-tips" v-if="isAuApp">auVPN App 掃碼導入</span>
                        </div>
                    </div>
                    <p class="qr-title" v-if="csCode.length > 0">{{$t('ClientTutorial.Need-Help')}}</p>
                    <span class="support-btn" target="_blank" v-if="csCode.length > 0" @click="goToSupport">{{$t('ClientTutorial.Contact-Customer-Service')}}</span>
                </div>
            </div>
        </div>
        <div class="client-tutorials-container-mobile" v-if="!isPc">
            <a-spin v-if="isArticleLoading || isError" class="client-tutorials-loading">
                <a-icon slot="indicator" type="loading" style="font-size: 70px" spin/>
            </a-spin>
            <!--<div v-if="!isArticleLoading" class="content-title" @click="showArticleList = !showArticleList">-->
            <div v-if="!isArticleLoading && !isError" class="content-title">
                <div class="system-list" ref="systemList" id="systemList"  @touchmove="systemListScroll">
                    <div class="system-container" :class="{'selected-system': index === systemIndex}"
                         v-for="(systemItem, index) in dataList" :key="index" @click="openList(index)">
                        <i class="system-icon" :class="'system-icon-'+ systemItem.name"></i>
                        <span class="system-name">{{systemItem.name}}</span>
                        <i class="down-icon" v-if="index === systemIndex" :class="{'up-icon':showArticleList}"></i>
                    </div>
                    <div class="door door-left"></div>
                    <div class="door door-right"></div>
                </div>
                <div class="tutorial-list" id="tutorial-list" :style="{'left': clientListLeft, 'opacity': showArticleList?1:0}" >
                    <div class="client-group" >
                        <div class="client-item-container client-item"
                             :class="{'client-item-selected': clientIndex === index}"
                             v-for="(clientItem, index) in dataList[systemIndex].articleList" :key="index"
                             @click="switchArticle(clientItem, index,true)">
                            <i class="client-icon"
                               :class="'client-icon-' + dataList[systemIndex].name + '-' + clientItem.name.toString().replace(' ', '')"></i>
                            <span class="client-name">{{clientItem.name}}</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="content-box-list-article">
                <div class="content-box-article">
                    <div class="tutorial-article">
                        <!--<p class="tutorial-article-name">{{selectedArticle.name}}</p>-->
                        <div class="tutorial-qr" v-if="subscribeUrl.length > 0">
                            <div v-if="selectedArticle.name" class="qr-container"
                                 :class="{'tutorial-qr-hide': (selectedArticle.name.toLowerCase().indexOf('whaleblue') > -1 || selectedArticle.name.toLowerCase().indexOf('jetstream') > -1)}">
                                <!--<p class="qr-title">{{selectedArticle.name}} {{$t('my/service:tutorial.Bullet')}}</p>-->
                                <p class="qr-title" v-if="selectedArticle.templateList.length === 1">{{$t('ClientTutorial.Subscription-Address')}}</p>
                                <a-select class="qr-title" style="width: 4.667rem;height: 0.667rem;" :placeholder="$t('ClientTutorial.Please-Select-Placeholder')"
                                          showSearch
                                          v-model="selectedTemplateId"
                                          @change="templateChange"
                                          v-if="selectedArticle.templateList.length > 1">
                                    <a-select-option :value="item.id"
                                                     v-for="(item,index) in selectedArticle.templateList" :key="index">
                                        {{'GFW List'}}
                                    </a-select-option>
                                </a-select>
                                <div class="sub-box">
                                    <div class="hide-box" v-if="serviceStatus !== 'Active'">购买服务后可见</div>
                                    <div class="qr-url-content">
                                        <span v-if="!isAuApp" class="qr-url-text notranslate" id="qr-url-text">{{subscribeUrl}}</span>
                                        <a-tooltip v-if="!isAuApp" placement="top" trigger="click" :mouseLeaveDelay=2
                                                   :destroyTooltipOnHide=true>
                                            <template slot="title">
                                                <span>{{$t('my/service:tutorial.Notice-1')}}</span>
                                            </template>
                                            <span v-if="!isAuApp" class="url-copy-btn"
                                                  @click="copySubUrl(selectedArticle.name)">{{$t('my/service:tutorial.Button')}}</span>
                                        </a-tooltip>
                                        <div class="i-qr-code" id="qrcodeMobile" ref="qrcodeMobile" v-if="isAuApp"></div>
                                        <span class="auapp-tips" v-if="isAuApp" @click="openAuApp()">安裝 App 後一鍵導入</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="articles-detail">
                            <SimpleArticle
                                    :article="selectedArticle.url"
                                    :isSimpleArticle="true"
                                    v-on:detailedTutorialId="detailedTutorialId">
                            </SimpleArticle>
                        </div>
                    </div>
                    <div class="support-box">
                        <p class="support-title">{{$t('my/service:plan.Notice-1')}}</p>
                        <a-button class="support-btn" type="primary" @click="openZw()">
                            {{$t('my/service:plan.Notice-2')}}
                        </a-button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import SimpleArticle from "@/views/SimpleArticle";
    import QRCode from 'qrcodejs2';
    import {Base64} from 'js-base64';
    import {AU_SELECTED_SERVICE, BASE_URL} from "../requestServer";
    import {TweenMax, Power0, Power1, Power2} from 'gsap';
    import _ from 'lodash';


    export default {
        name: "ClientTutorial",
        components: {
            SimpleArticle,
        },
        data() {
            return {
                isPc: this.$isPc(),
                isArticleLoading: true,
                isError: false,
                dataList: [],
                selectedArticle: {},
                selectedTemplateId: 0,
                subscribeUrl: '',
                showTips: false,
                showQrList: ['v2rayNG'],
                clientList: [
                    'jetstream',
                    'v2rayu',
                    'v2rayng',
                    'quantumult',
                    'quantumultx',
                    'shadowrocket',
                    'clash',
                    'surge',
                    'switchyomega',
                    'lede',
                    'merlin',
                    'gost',
                    'v2rayn'
                ],
                csCode: '',
                showArticleList: false,
                isAuApp: false,
                systemIndex: 0,
                systemIndexBuf: 0,
                clientIndex: 0,
                device: 'Windows',
                clientListLeft: 0
            }
        },
        props: {
            subUrl: {},
            embedCode: {},
            serviceStatus: {}
        },
        watch: {
            subUrl: {
                immediate: true,
                handler(value) {
                    this.subUrl = value;
                    this.getArticleList();
                }
            },
            embedCode: {
                immediate: true,
                handler(value) {
                    if (value !== undefined && value.CS_CODE !== undefined) {
                        this.csCode = value.CS_CODE;
                    }
                }
            },
            serviceStatus: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.serviceStatus = value;
                    }
                }
            }
        },
        created() {
            let uA = navigator.userAgent.toLowerCase();
            let ipad = uA.match(/ipad/i) !== null;
            let iphone = uA.match(/iphone/i) !== null;
            let android = uA.match(/android/i) !== null;
            let windows = uA.match(/windows/i) !== null;
            let mac = uA.match(/mac os/i) !== null;
            this.device = ipad || iphone ? 'iOS' : android ? 'Android' : windows ? 'Windows' : mac ? 'macOS' : 'Windows'

        },
        mounted() {
        },
        methods: {
            systemListScroll(){
                this.showArticleList = false
            },
            openAuApp() {
                let selectedService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                let hmac = selectedService.hmac;
                let sid = selectedService.id;
                let domain = BASE_URL.replace('www.', '')
                window.location.href = `auvpn://config?${domain}/wl/v1/subscribe/${sid}?hmac=${hmac}`;
            },
            openZw() {
                window.zE('webWidget', 'open');
                //setTimeout(function () {
                // Open the channel
                //    document.getElementsByClassName('zEWidget-webWidget--active')[0].contentWindow.document.getElementsByTagName('footer')[0].getElementsByTagName('button')[0].click();
                //}, 1500);
            },
            goToSupport() {
                window.open(this.csCode, this.$inApp ? '_self' : '_blank');
            },
            detailedTutorialId(id) {
                this.$emit('detailedTutorialId', id);
            },
            qrcode() {
                let selectedService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                let hmac = selectedService.hmac;
                let sid = selectedService.id;
                let domain = BASE_URL.replace('www.', '')
                new QRCode(this.isPc ? 'qrcode' : 'qrcodeMobile', {
                    width: 150, // 设置宽度，单位像素
                    height: 150, // 设置高度，单位像素
                    // text: "{ \"uuid\":\"1a78efd0-3825-4f54-b8ed-d118a6e678c4\", \"hmac\":\"0a8939edb4fd2b8e433275fa6e8b36a6d0f5fc926a02fc565bc8cb3097b86708\", \"email\":\"\" }"   // 设置二维码内容或跳转地址
                    text: `${domain}/wl/v1/subscribe/${sid}?hmac=${hmac}`
                });
            },
            getArticleList() {
                let that = this;
                this.$request.getArticlesList(function (data) {
                    that.isArticleLoading = false;
                    if (data && data.status === 200) {
                        that.isArticleLoading = false;
                        that.dataList = data.data;
                        let selectedItem = null
                        that.dataList.sort((a, b) => {
                            return a.sequence - b.sequence;
                        });
                        that.dataList.forEach((item, index) => {

                            // set this with user's device
                            item['isShow'] = item.name === that.device;
                            item.articleList.sort((a, b) => {
                                return a.sequence - b.sequence;
                            });
                            item.articleList.forEach(citem => {
                                citem['isShow'] = false;
                            });

                            if (item.name === that.device) {
                                selectedItem = item
                                item.articleList[0].isShow = true;
                                that.systemIndex = index
                            }
                        });

                        // set default
                        if (selectedItem) {
                            that.selectedArticle = selectedItem.articleList[0];
                        } else {
                            that.dataList[0].articleList[0].isShow = true;
                            that.selectedArticle = that.dataList[0].articleList[0];
                        }

                        if (that.selectedArticle.templateList.length > 0) {
                            that.selectedTemplateId = that.selectedArticle.templateList[0].id;
                            that.templateChange(that.selectedArticle.templateList[0].id);
                        }

                        that.scrollSystemItem(that.systemIndex, false)
                    } else {
                        that.isError = true;
                    }
                });
            },
            openClient(itemIndex) {
                this.dataList.forEach((item, index) => {
                    if (index === itemIndex) {
                        item.isShow = true;
                        this.switchArticle(item.articleList[0], 0, false);
                    } else {
                        item.isShow = false;
                    }
                });
                //if (!this.isPc) this.showArticleList = !this.showArticleList;
            },
            switchArticle(item, index, articleShow) {
                this.clientIndex = index
                this.dataList.forEach(section => {
                    section.articleList.forEach((article, aindex) => {
                        article.isShow = (item.articleId === article.articleId && index === aindex);
                    })
                });
                this.selectedArticle = item;
                if (this.selectedArticle.templateList.length > 0) {
                    this.selectedTemplateId = this.selectedArticle.templateList[0].id;
                    this.templateChange(this.selectedArticle.templateList[0].id);
                }
                if (!this.isPc && articleShow) this.showArticleList = !this.showArticleList;
            },
            templateChange(value) {
                this.subscribeUrl = this.subUrl.length > 0 ? `${this.subUrl}&templateId=${value}` : '';
                // display only AU app
                if (value === 15) {
                    this.isAuApp = true
                    if (this.$refs.qrcodeMobile) {
                        this.$refs.qrcodeMobile.innerHTML = "";
                    }
                    this.$nextTick(() => {
                        this.qrcode();
                    })
                } else {
                    this.isAuApp = false
                }
            },
            copySubUrl(clientName) {
                let input = document.createElement("input");
                input.value = this.subscribeUrl;
                document.body.appendChild(input);
                input.select(); // 选择对象
                document.execCommand("Copy"); // 执行浏览器复制命令
                input.className = "input";
                input.style.display = "none";
                this.showTips = true

                switch (clientName.toLowerCase()) {
                    case 'clashx':
                        window.location.href = `ClashX://install-config?url=${encodeURIComponent(this.subscribeUrl)}`;
                        break;
                    case 'quantumult':
                        window.location.href = `quantumult://configuration?server=${Base64.encode(this.subscribeUrl + '&protocol=https&mode=chinaDirectOut')}`;
                        break;
                    //case 'quantumult x':
                    //  window.location.href = `quantumult-x:///update-configuration?remote-resource=%7b%22server_remote%22%3a%5b%22https%3a%2f%2fapi.savoy.click%2fapi%2fv1%2fservices%2f18296%2fsubscription%2fv2%2f6661f84478f1ba5b8d360465e89304ac%3fclient%3dquantumultx%2c+tag%3djetstream%22%5d%7d`;
                    //  break;
                    case 'shadowrocket':
                        window.location.href = `sub://${Base64.encode(this.subscribeUrl)}#jetstream`;
                        break;
                    case 'clash':
                        window.location.href = `clash://install-config?url=${encodeURIComponent(this.subscribeUrl)}`;
                        break
                }
            },
            openList(index) {
                this.systemIndexBuf = _.cloneDeep(this.systemIndex)
                if (this.systemIndex !== index) {
                    this.switchArticle(this.dataList[index].articleList[0], index, true)
                    this.showArticleList = false
                    this.clientIndex = 0
                    this.scrollSystemItem(index, true)
                } else {
                    this.showArticleList = !this.showArticleList;
                }

                this.systemIndex = index

            },
            // scroll system item to middle
            scrollSystemItem(index, isShow) {

                let systemItems = document.getElementsByClassName('system-container')
                let iconItems = document.getElementsByClassName('down-icon')
                if (systemItems.length > 0 && (index !== 0 || index !== this.dataList.length - 1)) {
                    // consider the down-icon only display when selected
                    const d = systemItems.item(index).offsetLeft - (index > this.systemIndex ? iconItems.item(0).clientWidth : 0)
                    const c = systemItems.item(index).clientWidth + (index !== this.systemIndex ? iconItems.item(0).clientWidth : 0)
                    const b = document.body.clientWidth
                    // console.log(systemItems.item(index).getBoundingClientRect().left)
                    const that = this

                    TweenMax.fromTo(
                        document.getElementById('systemList'),
                        0.3,
                        {scrollLeft: this.$refs.systemList.scrollLeft},
                        {
                            scrollLeft: d + (c) / 2 - b / 2,
                            onComplete: function () {
                                if (that.systemIndexBuf !== that.systemIndex) {
                                    let list = document.getElementById('tutorial-list')
                                    that.clientListLeft = String(systemItems.item(index).getBoundingClientRect().left + c/2 - list.clientWidth/2) + 'px'
                                    that.showArticleList = isShow
                                }
                            }
                        },
                    );
                }

                if (index === 0) {

                } else if (index === this.dataList.length - 1) {

                }
            }
        }
    }
</script>

