        flex-direction: row;
        width: 1080px;
        //height: 80%;
        margin: 0px auto;

        .client-tutorials-loading {
            height: 500px;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #fff;
        }

