            width: 214px;
            background-color: #FCFDFD;
            padding: 40px 35px 40px 40px;
            border-top-left-radius: 8px;
            border-bottom-left-radius: 8px;
            box-shadow: 0 0 6px rgba(234, 238, 246, 0.8);
            z-index: 3;

            .client-group {
                /*.client-item {*/
                /*border-bottom: 1px solid #E5E9ED;*/
                /*&:nth-of-type(1) {*/
                /*border-top: 1px solid #E5E9ED;*/
                /*}*/
                /*}*/
                .client-item-container {
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    text-align: left;
                    margin-top: 16px;

                    &:first-child {
                        margin-top: 0;
                    }

                    .left-checked-icon {
                        background: url('../assets/service/ic-blue-rectangle.svg') center no-repeat;
                        background-size: contain;
                        width: 5px;
                        height: 28px;
                        position: absolute;
                        margin-left: -40px;
                    }

                    .item-header {
                        cursor: pointer;
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: space-between;
                        margin-bottom: 20px;

                        .client-title {
                            cursor: pointer;
                            display: flex;
                            align-items: center;
                            font-size: 20px;
                            font-weight: 600;
                            line-height: 30px;
                            /*height: 66px;*/
                            margin-bottom: 0;
                            max-width: 113px;
                            color: #A5B1BF;
                        }

                        .checked-client-title {
                            color: #3963EF;
                        }

                        .item-open-icon-close {
                            animation: mymove 0.15s;
                            animation-fill-mode: forwards;
                        }

                        .item-open-icon-open {
                            animation: mymove2 0.15s;
                            animation-fill-mode: forwards;
                        }

                        @keyframes mymove {
                            from {
                                transform: rotate(0deg);
                            }
                            to {
                                transform: rotate(45deg);
                            }
                        }
                        @keyframes mymove2 {
                            from {
                                transform: rotate(45deg);
                            }
                            to {
                                transform: rotate(0deg);
                            }
                        }

                        .item-close-icon {
                            background: url('../assets/service/qa-close.svg') center no-repeat;
                            background-size: contain;
                            width: 14px;
                            display: inline-block;
                            height: 14px;
                        }

                        &:hover {
                            .qa-title {
                                color: #3963EF;
                            }

                            .item-close-icon {
                                /*background: center no-repeat ;*/
                                background: url('../assets/service/qa-close.svg') center no-repeat;
                                background-size: contain;
                            }
                        }

                        .client-item-enter-active {
                            transition: transform .3s ease;
                        }

                        .client-item-leave-active {
                            transition: transform .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
                        }

                        .client-item-enter, .client-item-leave-to
                            /* .slide-fade-leave-active for below version 2.1.8 */
                        {
                            transform: translateX(10px);
                            opacity: 0;
                        }

                        &:hover {
                            cursor: pointer;

                            .client-title {
                                color: #3963EF;
                            }
                        }
                    }

                    .item-header-opacity {
                        //opacity: 0.4;
                    }

                    .content-list {
                        max-height: fit-content;
                        height: 0;
                        overflow: hidden;
                        transition: height ease-out 0.2s;

                        .client-name-content {
                            display: flex;
                            flex-direction: row;
                            align-items: center;
                            margin-bottom: 20px;

                            .client-icon {
                                display: inline-block;
                                content: '';
                                width: 20px;
                                height: 20px;
