<template>
  <div>
    <div class="article-loading" :style="{'min-height':isSimpleArticle ? '500px' : '100vh'}" v-if="isLoading && isPc">
      <a-spin>
        <a-icon slot="indicator" type="loading" :style="{'font-size': isSimpleArticle ? '80px' : '120px'}" spin/>
      </a-spin>
    </div>
    <div class="article-loading" :style="{'min-height': '100vh'}" v-if="isLoading && !isPc">
      <a-spin>
        <a-icon slot="indicator" type="loading" :style="{'font-size': '80px'}" spin/>
      </a-spin>
    </div>
    <p class="tutorial-article-name" v-if="!isLoading">{{name}}</p>
    <div v-if="!isLoading" class="article-body" :class="{'article-body-mb':!isPc}" v-html="articleBody">
    </div>
  </div>

</template>

<script>
  import $ from 'jquery';

  export default {
    name: 'SimpleArticle',
    data() {
      return {
        isPc : this.$isPc(),
        articleBody: '',
        articleId: 0,
        isLoading: true,
        name: '',
      };
    },
    props: {
      article: {},
      isSimpleArticle: {},
    },
    watch: {
      article: {
        immediate: true,
        handler(val) {
          if (val) {
            this.isLoading = true;
            let that = this;
            this.$request.getArticleDetails(val,function (data) {
              if (data && data.status === 200 && data.data && data.data.article) {
                  that.name = data.data.article.name;
                  that.articleBody = data.data.article.body;
                  that.articleHandler(data.data.article.body);
              }
            });
          }
        }
      },
      isSimpleArticle: {
        immediate: true,
        handler(val) {
          if (val !== undefined) {
             this.isSimpleArticle = val;
          }
        }
      }
    },
    methods: {
      articleHandler(data) {
        // replace the resource
        this.articleBody = data.replace(/whaleblue\.zendesk\.com/g, 'sg.inforun.work');
        // replase the url for order and users services
        this.articleBody = this.articleBody.replace(/https:\/\/jetstream\.surf\/WHMCS\/cart\.php\?.+language=chinese/g, `${window.location.origin}/order/place`);
        this.articleBody = this.articleBody.replace(/https:\/\/jetstream\.surf\/WHMCS\/clientarea\.php\?language=chinese/g, `${window.location.origin}/my/service`);
        // this.articleBody = this.articleBody.replace(/https:\/\/my\.whaleblue\.io/g, `${window.location.origin}/my/service`)
        this.articleBody = this.articleBody.replace(/https:\/\/jetstream\.surf/g, `${window.location.origin}`);
        this.articleBody = this.articleBody.replace(/https:\/\/jetstream\.xyz/g, `${window.location.origin}`);

        if (!!this.articleId && this.articleId != 0) {
          // if user in the help
          this.articleBody = this.articleBody.replace(/(https:\/\/sg\.inforun\.work){1}(.*)articles{1}\//g, '');
        } else {
          let lang = 'zh-tw';//this.$i18n.locale;
          // if user in the dashboard
          this.articleBody = this.articleBody.replace(/(https:\/\/sg\.inforun\.work){1}(.*)articles{1}\//g, '/' + lang + '/help/article/');
        }
        //https://jetstream.zendesk.com/hc/zh-tw/articles/360048796032
        //this.articleBody = this.articleBody.replace(/(https:\/\/jetstream\.zendesk\.com\/hc\/zh-tw){1}/g, `${window.location.origin}`);

        const that = this;
        setTimeout(function () {
          let aList = document.getElementsByTagName('a');
          if (aList.length > 0) {
            that.addEventForLastA();
          }
        }, 500);
        // setting the animation
        setTimeout(function () {
          $('.accordion__item-title')
            .on('click', function () {
              let $title = $(this);
              $title.toggleClass('accordion__item-title--active');
              $title.parents('.accordion__item')
                .find('.accordion__item-content')
                .slideToggle();
            });
          let HC_SETTINGS = {
            css: {
              activeClass: 'is-active',
              hiddenClass: 'is-hidden'
            }
          };
          $('.tabs-link')
            .click(function (e) {
              e.preventDefault();
              let $link = $(this);
              let tabIndex = $link.index();
              let $tab = $link.parents('.tabs')
                .find('.tab')
                .eq(tabIndex);
              $link.addClass(HC_SETTINGS.css.activeClass)
                .siblings()
                .removeClass(HC_SETTINGS.css.activeClass);
              $tab.removeClass(HC_SETTINGS.css.hiddenClass)
                .siblings('.tab')
                .addClass(HC_SETTINGS.css.hiddenClass);
            });
        }, 1000);
        this.isLoading = false
      },
      addEventForLastA() {
        if (this.isSimpleArticle || !this.isSimpleArticle) {
          let aList = document.getElementsByTagName('a');
          aList.forEach((item,p) => {
            let aTag = aList[p];
            let url = aTag.href;
            if (url.toString().length > 0 && /(.*){1}\/hc\/(.*)articles{1}\//.test(url)) {
              $(`a:eq(${p})`).removeAttr("href");
              $(`a:eq(${p})`).removeAttr("target");
              $(`a:eq(${p})`).removeAttr("rel");
              const that = this;
              aTag.addEventListener('click',function () {
                let id = url.substring(url.lastIndexOf("/") + 1);
                if (that.isSimpleArticle) {
                  that.$emit('detailedTutorialId',id);
                } else {
                  that.$emit('otherTutorialId',id);
                }
              });
            }
          });
          /*let last = aList.length - 1;
          let lastA = aList[last];
          let url = lastA.href;
          $("a:last").removeAttr("href");
          $('a:last').removeAttr("target");
          $('a:last').removeAttr("rel");
          const that = this;
          lastA.addEventListener('click',function () {
            let id = url.substring(url.lastIndexOf("/") + 1);
            if (that.isSimpleArticle) {
              that.$emit('detailedTutorialId',id);
            } else {
              that.$emit('otherTutorialId',id);
            }
          });*/
        }
      }
    }
  };
</script>

