<script>
    import ClientTutorial from "@/views/ClientTutorial";
    import DetailedArticle from "@/views/DetailedArticle";
    import ResetServiceDialog from "@/dialog/ResetServiceDialog";
    import MBServiceDialog from "@/dialog/MBServiceDialog";
    import {AU_EMAIL, AU_EMAIL_STATUS, AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "@/requestServer";
    import {notification} from "ant-design-vue";
    //import $ from "jquery";
    export default {
        name: "UserServicePanel",
        components: {
            ClientTutorial,
            DetailedArticle,
            ResetServiceDialog,
            MBServiceDialog
        },
        data () {
            return {
                isLogin: localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC),
                isLoading: true,
                isNetworkError: false,
                isShowMessageBox: false,
                infoError: false,
                apiDevelopError: false,
                isClickShare: false,
                isClickResetService: false,
                isClickBindEmail: false,
                isStartBindEmail: false,
                emailErrorMsg: '',
                emailReg: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                secondNum: 30,
                checkTimer: 0,
                totalCheckCount: 0,
                isCountTimeEnd: false,
                showMsg: false,
                testData: ['a','b','c','c','a','a'],
                usageArray:['a','b','c','c','a','a','b','c','c'],
                selectedItem: {},
                settingsInfo: {},
                currentTime : new Date().getTime(),
                trafficArray: [{trafficLimit:'∞',speedLimit: '∞',isAddon:false,sessionLimit:'∞'}],
                speedArray: ['速度','∞','','','',''],
                detailedUrl: '',
                lang: this.$i18n.locale,
                iconLoading: false,
                isPc : this.$isPc(),
                pageYOffset: 0,
                currentService:{},
                renewTimer: 0,
            }
        },
        created() {
            if (this.isLogin) {
                if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                    let id = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id : 0 ;
                    this.currentService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                    if (this.$route.params.serviceId === 0) this.$router.push({params:{'serviceId': id}, query: that.$route.query});
                    if (this.currentService.status !== 'Pending') {
                        this.getServiceInfo();
                        this.getSettingInfo();
                    } else {
                        //this.isLoading = false;
                    }
                    //force update current service info
                    const that = this;
                    this.$request.auGetAllServices('','',function (data) {
                        if (data && (data.status === 200 || data.status === 201) && data.data) {
                            localStorage.setItem(AU_EMAIL, data.data.email);
                            localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                            if (data.data.jjService && data.data.jjService.length > 0) {
                                let OldStatusIsPending = that.currentService.status === 'Pending';
                                let findService = data.data.jjService.find(item => (item.relid === (that.currentService.status !== 'Pending' ? that.currentService.relid : that.currentService.id)));
                                if (findService !== undefined) {
                                    that.currentService = findService;
                                    localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(that.currentService));
                                    that.$eventBus.$emit("update_header", true);
                                    if (OldStatusIsPending) {
                                        that.getServiceInfo();
                                        that.getSettingInfo();
                                    }
                                } else {
                                    that.isLoading = false;
                                }
                            } else {
                                that.isLoading = false;
                            }
                        } else {
                            that.isLoading = false;
                        }
                    });
                } else {
                    const that = this;
                    this.$request.auGetAllServices('','',function (data) {
                        if (data && (data.status === 200 || data.status === 201) && data.data) {
                            localStorage.setItem(AU_EMAIL, data.data.email);
                            localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                            if (data.data.jjService && data.data.jjService.length > 0) {
                                // Need to change logic to use new api.
                                const activeServices = data.data.jjService.filter(service => service.status === 'Active')
                                localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(activeServices.length > 0 ? activeServices.pop() : data.data.jjService[0]));
                                that.currentService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                              if (that.$route.params.serviceId === 0) that.$router.push({params:{'serviceId': that.currentService.id}, query: that.$route.query});
                                that.getServiceInfo();
                                that.getSettingInfo();
                            } else if (data.data.unpaidService && data.data.unpaidService.length > 0) {
                                that.isLoading = false;
                                localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(data.data.unpaidService[0]));
                                that.currentService = data.data.unpaidService[0];
                            } else {
                                that.isLoading = false;
                            }
                        } else {
                            that.isLoading = false;
                        }
                    });
                }
            } else {
                this.isLoading = false;
            }
        },
        mounted() {
            // event fire
            this.$eventFire.viewUserCenter()
        },
        methods: {
            getPlanTitle() {
                this.lang = this.$i18n.locale;
                if (this.selectedItem.planName) {
                    if (this.lang === 'zh-CN' && this.selectedItem.planName['zh-cn']) {
                        return this.selectedItem.planName['zh-cn'];
                    } else if (this.lang === 'zh-TW' && this.selectedItem.planName['zh-tw']) {
                        return this.selectedItem.planName['zh-tw'];
                    } else if (this.selectedItem.planName['en']) {
                        return this.selectedItem.planName['en'];
                    }
                }
                return '';
            },
            getExpireDate() {
                let regArray = this.currentService.regdate.split('-');
                let year = parseInt(regArray[0]);
                let mon = parseInt(regArray[1]);
                let day = parseInt(regArray[2]);
                if (this.currentService.billingcycle === 'Annually') {
                    year = parseInt(year) + 1;
                } else if (this.currentService.billingcycle === 'Monthly') {
                    year = mon + 1 > 12 ? year + 1 : year;
                    mon = mon + 1 > 12 ? (mon - 11) : mon + 1;
                } else {
                    year = mon + 6 > 12 ? year + 1 : year;
                    mon = mon + 6 > 12 ? (mon - 11) : mon + 6;
                }
                return year + '-' + (mon >= 10 ? mon : '0' + mon )  + '-' + (day >= 10 ? day : '0' + day);
            },
            closeDialog(result,newUrl){
                this.isClickResetService = false;
                this.isClickShare = false;
                this.isClickBindEmail = false;

                if (!this.isPc) {
                    window.scrollTo({ top: this.pageYOffset, left: 0, behavior: 'smooth' });
                }
                if (result){
                    //window.location.href = newUrl;
                    const that = this;
                    this.$request.auGetAllServices('','',function (data) {
                        if (data && (data.status === 200 || data.status === 201) && data.data) {
                            if (data.data.jjService && data.data.jjService.length > 0) {
                                if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                                    data.data.jjService.forEach(item => {
                                        if (item.id === JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id) {
                                            localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(item));
                                            that.getServiceInfo();
                                            that.getSettingInfo();
                                        }
                                    });
                                }
                            }
                        }
                    });
                }
            },
            detailedTutorialId(id) {
                if (!this.isPc) {
                    window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
                }
                this.detailedUrl = `api/v2/help_center/zh-tw/articles/${id}.json`;
            },
            getHistogramH(item) {
                if (this.isPc) {
                    return 117.5 / ((this.speedArray[1] === '∞' ? 10 : this.speedArray[1]) / 4 + (this.speedArray[1] === '∞' ? 10 : this.speedArray[1])) * (item.speedLimit === '∞' ? 10 : item.speedLimit === 'addon' ? (this.speedArray[1] === '∞' ? 10 : this.speedArray[1]) * (1 + 1/4) : item.speedLimit);
                } else {
                    return 3.3 / ((this.speedArray[1] === '∞' ? 10 : this.speedArray[1]) / 4 + (this.speedArray[1] === '∞' ? 10 : this.speedArray[1])) * (item.speedLimit === '∞' ? 10 : item.speedLimit === 'addon' ? (this.speedArray[1] === '∞' ? 10 : this.speedArray[1]) * (1 + 1/4) : item.speedLimit);
                }
            },
            getHistogramW(index,item) {
                if (this.isPc) {
                    return this.trafficArray.length === 1 ? 170 : (index !== (this.trafficArray.length - 1) ? (170 / this.trafficArray[this.trafficArray.length - 1].trafficLimit) * (this.trafficArray[index + 1].trafficLimit - item.trafficLimit) : 25);
                } else {
                    return this.trafficArray.length === 1 ? 6.667 : (index !== (this.trafficArray.length - 1) ? (6.667 / this.trafficArray[this.trafficArray.length - 1].trafficLimit) * (this.trafficArray[index + 1].trafficLimit - item.trafficLimit) : 0.6);
                }
            },
            getUsageP(item,index) {
                if (this.isPc) {
                    const maxLimit = (this.selectedItem.trafficDn !== undefined && this.selectedItem.trafficUp !== undefined) ? (parseFloat(this.selectedItem.trafficDn + this.selectedItem.trafficUp) / Math.pow(1024,3) * 3) .toFixed(2) : 500;
                    const totalLimit = this.trafficArray[this.trafficArray.length - 1].trafficLimit === '∞' ? maxLimit : this.trafficArray[this.trafficArray.length - 1].trafficLimit;
                    const padding = (170 / totalLimit) * this.getUsage(item,index);
                    return padding > 170 ?  50 : padding;
                } else {
                    const maxLimit = (this.selectedItem.trafficDn !== undefined && this.selectedItem.trafficUp !== undefined) ? (parseFloat(this.selectedItem.trafficDn + this.selectedItem.trafficUp) / Math.pow(1024,3) * 3) .toFixed(2) : 6.667;
                    const totalLimit = this.trafficArray[this.trafficArray.length - 1].trafficLimit === '∞' ? maxLimit : this.trafficArray[this.trafficArray.length - 1].trafficLimit;
                    const padding = (6.667 / totalLimit) * this.getUsage(item,index);
                    return padding > 6.667 ?  0.6 : padding;
                }
            },
            getUsageH() {
                if (this.isPc) {
                    return 117.5 / ((this.speedArray[1] === '∞' ? 10 : this.speedArray[1]) / 4 + (this.speedArray[1] === '∞' ? 10 : (this.speedArray[1] === '∞' ? 10 : this.speedArray[1])))
                        * (this.selectedItem.speedDeviceLimit[0].speedLimit === '∞' ? 10 : this.selectedItem.speedDeviceLimit[0].speedLimit === 'addon' ? (this.speedArray[1] === '∞' ? 10 : this.speedArray[1]) * (1 + 1/4) : this.selectedItem.speedDeviceLimit[0].speedLimit);
                } else {
                    return 3.3 / ((this.speedArray[1] === '∞' ? 10 : this.speedArray[1]) / 4 + (this.speedArray[1] === '∞' ? 10 : (this.speedArray[1] === '∞' ? 10 : this.speedArray[1])))
                        * (this.selectedItem.speedDeviceLimit[0].speedLimit === '∞' ? 10 : this.selectedItem.speedDeviceLimit[0].speedLimit === 'addon' ? (this.speedArray[1] === '∞' ? 10 : this.speedArray[1]) * (1 + 1/4) : this.selectedItem.speedDeviceLimit[0].speedLimit);
                }

            },
            getUsage(item,index) {
                if (this.selectedItem.trafficDn !== undefined && this.selectedItem.trafficUp !== undefined) {
                    const totalUsage = (parseFloat(this.selectedItem.trafficDn + this.selectedItem.trafficUp) / Math.pow(1024,3)).toFixed(2);
                    if (index !== this.trafficArray.length - 1) {
                        if (totalUsage >= parseFloat(item.trafficLimit) && totalUsage < parseFloat(this.trafficArray[index + 1].trafficLimit)){
                            return totalUsage - parseFloat(item.trafficLimit);
                        }
                    } else {
                        if (totalUsage >= parseFloat(item.trafficLimit)){
                            return totalUsage - parseFloat(item.trafficLimit);
                        } else if (this.trafficArray.length === 1 && item.trafficLimit === '∞') {
                            return totalUsage;
                        }
                    }
                    return totalUsage;
                } else {
                    return 0;
                }
            },
            showUsage(item,index) {
                if (this.selectedItem.trafficDn !== undefined && this.selectedItem.trafficUp !== undefined) {
                    const totalUsage = (parseFloat(this.selectedItem.trafficDn + this.selectedItem.trafficUp) / Math.pow(1024,3)).toFixed(2);
                    if (index !== this.trafficArray.length - 1) {
                        return totalUsage >= parseFloat(item.trafficLimit) && totalUsage < parseFloat(this.trafficArray[index + 1].trafficLimit);
                    } else {
                        return totalUsage >= parseFloat(item.trafficLimit) || (this.trafficArray.length === 1 && item.trafficLimit === '∞');
                    }
                } else {
                    return false;
                }
            },
            formatDate(value) {
                let date = new Date(value);
                let y = date.getFullYear();
                let MM = date.getMonth() + 1;
                MM = MM < 10 ? ('0' + MM) : MM;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                return y + '-' + MM + '-' + d;
            },
            getSettingInfo() {
                let that = this;
                this.$request.getSettingInfo(function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        that.settingsInfo = data.data;
                    }
                });
            },
            getServiceInfo() {
                this.trafficArray = [{trafficLimit:'0',speedLimit: 0,isAddon:false,sessionLimit:'∞'}];
                this.speedArray = ['速度','','','','',''];
                let that = this;
                this.$request.getServiceInfo(function (data) {
                    that.isLoading = false;
                    if (data && data.status === 200) {
                        that.currentService.status = data.data.status;
                        that.selectedItem = data.data;
                        let dashUrl = that.selectedItem.dashboardUrl;
                        that.selectedItem['usageDashboardUrl'] = dashUrl.substring(0,dashUrl.lastIndexOf("&token"));
                        that.selectedItem.email =  that.selectedItem.email ? that.selectedItem.email.endsWith('@auvpn.net') && that.selectedItem.email.length > 40 ? '' : that.selectedItem.email : '';
                        if ((that.isClickBindEmail && that.isStartBindEmail && that.selectedItem.emailStatus === 'Binded') || localStorage.getItem('verifyEmail')) {
                            that.isStartBindEmail = false;
                            that.emailErrorMsg = '';

                            that.bindEmail();
                            localStorage.removeItem('verifyEmail');

                            that.isShowMessageBox = !that.isShowMessageBox;
                            let msgTimer = setInterval(() => {
                                if (that.isShowMessageBox) {
                                    window.clearInterval(msgTimer);
                                    that.isShowMessageBox = !that.isShowMessageBox;
                                }
                            }, 2000);
                        }
                        that.selectedItem['speedDeviceLimit'] = [];
                        let speeds = [];
                        that.selectedItem.limitRuleDtos.sort((a,b) => {
                            return a.trafficLimit - b.trafficLimit;
                        });
                        const addon = that.selectedItem.addon / Math.pow(1024,3);
                        if (addon > 0) {
                            that.trafficArray[0].isAddon = true;
                            that.trafficArray.push({trafficLimit:addon,speedLimit: 0,isAddon:false,sessionLimit:0});
                        }
                        if (that.selectedItem.limitRuleDtos.length > 0) {
                            that.selectedItem.limitRuleDtos.forEach((item,index) => {
                                item.trafficLimit /= Math.pow(1024,3);
                                item.speedLimit = item.speedLimit / Math.pow(1024,2) * 8;
                                item.trafficLimit += addon;
                                if (index === 0) {
                                    if (item.trafficLimit === 0) {
                                        that.trafficArray[0].speedLimit = item.speedLimit;
                                        that.trafficArray[0].sessionLimit = item.sessionLimit;
                                        speeds.push(item.speedLimit);
                                    } else {
                                        that.trafficArray.push(item);
                                        speeds.push(item.speedLimit);
                                        that.trafficArray[0].speedLimit = 'addon';
                                        if (addon > 0) that.trafficArray[1].speedLimit = 'addon';
                                    }
                                } else {
                                    that.trafficArray.push(item);
                                    speeds.push(item.speedLimit);
                                }
                            });
                            speeds.sort((a,b) => {
                                return b - a;
                            });
                            if (speeds.length > 0) {
                                if (speeds[0] > 0) {
                                    that.speedArray[1] = speeds[0];
                                    that.speedArray[3] = speeds[0] / 2;
                                } else {
                                    that.speedArray[1] = '∞';
                                }
                            }
                        } else {
                            that.trafficArray[0].speedLimit = '∞';
                            if (addon > 0) that.trafficArray[1].speedLimit = '∞';
                            that.speedArray[1] = '∞';
                            if (that.trafficArray.length === 1) that.trafficArray[0].trafficLimit = '∞';
                        }
                        //确认当前适合的规则
                        const totalUsage = (parseFloat(that.selectedItem.trafficDn + that.selectedItem.trafficUp) / Math.pow(1024,3)).toFixed(2) ;
                        that.trafficArray.forEach((item,index) => {
                            if (index !== that.trafficArray.length - 1) {
                                if (totalUsage >= parseFloat(item.trafficLimit) && totalUsage < parseFloat(that.trafficArray[index + 1].trafficLimit)){
                                    that.selectedItem['speedDeviceLimit'].push(item);
                                }
                            } else {
                                if (totalUsage >= parseFloat(item.trafficLimit)){
                                    that.selectedItem['speedDeviceLimit'].push(item);
                                }
                            }
                        });
                        if (that.selectedItem['speedDeviceLimit'].length === 0) {
                            that.selectedItem['speedDeviceLimit'].push(that.trafficArray[that.trafficArray.length - 1]);
                        }
                    } else {
                        /*that.infoError = true;
                        if (data && data.status === 502) {
                            that.apiDevelopError = true;
                        }*/
                    }
                });
            },
            shareService() {
                this.pageYOffset = window.pageYOffset
                if (!this.isPc) {
                    window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
                }
                this.isClickShare = true;
                this.isClickResetService = false;
                this.isClickBindEmail = false;
            },
            resetService() {
                this.pageYOffset = window.pageYOffset
                if (!this.isPc) {
                    window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
                }
                this.isClickResetService = true;
                this.isClickShare = false;
                this.isClickBindEmail = false;
                //const that = this;
                /*this.$request.resetService(function (data) {
                    if (data.status === 200 && data.data) {
                        window.location.href = data.data.dashboardUrl;
                        //let newUrl = data.data.dashboardUrl.slice(1).split('&');
                        //that.saveNewInfo(data.data.dashboardUrl.split('?')[1].split('&'));
                    }
                });*/
            },
            bindEmail() {
                this.pageYOffset = window.pageYOffset
                if (!this.isPc) {
                    window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
                }
                this.isClickBindEmail = true;
                this.isClickResetService = false;
                this.isClickShare = false;
                this.emailErrorMsg = ''
            },
            emailChange() {
                if (this.selectedItem.email) {
                    this.emailErrorMsg = ''
                }
            },
            clickBindEmail() {
                if (this.selectedItem.email) {
                    if (this.emailReg.test(this.selectedItem.email)) {
                        let that = this;
                        this.$request.bindEmail(this.selectedItem.email, function (data) {
                            if (data.status === 200) {
                                that.isStartBindEmail = true;
                                that.startCountTime();
                                that.emailErrorMsg =  this.$t("Verify-Email-Send");
                            } else {
                                that.emailErrorMsg = this.$t("Request-Error");
                            }
                        });
                    } else {
                        this.emailErrorMsg = this.$t("Please-Valid-Email");
                    }
                } else {
                    this.emailErrorMsg = this.$t("Please-Valid-Email");
                }
            },
            revokeEmail() {
                if (this.checkTimer > 0) {
                    window.clearInterval(this.checkTimer);
                    this.checkTimer = 0;
                    this.totalCheckCount = 0;
                }
                this.isStartBindEmail = false;
                this.selectedItem.email = '';
                this.emailErrorMsg = '';
            },
            clickUnbindEmail() {
                let that = this;
                this.$request.unBindEmail(this.selectedItem.email, function (data) {
                    if (data.status === 200) {
                        that.getServiceInfo();
                    }
                });
            },
            startCountTime() {
                this.isCountTimeEnd = false;
                let test = setInterval(() => {
                    this.secondNum--;
                    if (this.secondNum === 0 || this.selectedItem.emailStatus === 'Binded' || !this.isStartBindEmail) {
                        window.clearInterval(test);
                        this.secondNum = 30;
                        this.isCountTimeEnd = true;
                        if (this.selectedItem.emailStatus === 'Binded') {
                            this.isStartBindEmail = false;
                        }
                    } else if (this.secondNum === 10) {
                        this.emailErrorMsg = '';
                    }
                    if (this.secondNum === 25) {
                        if (this.checkTimer === 0) {
                            this.checkTimer = setInterval(() => {
                                this.totalCheckCount++;
                                if (!this.isStartBindEmail || this.selectedItem.emailStatus === 'Binded' || this.totalCheckCount === 15 || this.checkTimer === 0) {
                                    window.clearInterval(this.checkTimer);
                                    this.checkTimer = 0;
                                    this.totalCheckCount = 0;
                                } else {
                                    this.getServiceInfo();
                                }
                            }, 4000);
                        }
                    }
                }, 1000);
            },
            goToOrderProduct() {
                window.open(`${window.location.origin}/order/place`,this.$inApp? '_self': '_blank');
            },
            goToSupport() {
                window.open(this.settingsInfo.embedCode.CS_CODE, this.$inApp? '_self': '_blank');
            },
            goToSupportOrResetService() {
                this.currentService.inactiveReasons.length === 1 && this.currentService.inactiveReasons.includes('HackStop') ? this.resetService() : this.goToSupport()
            },
            goToRenew() {
                this.iconLoading = true;
                const that = this;
                let selectedService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                let relid = this.currentService.status === 'Pending' ? this.currentService.id: selectedService.relid;
                this.$request.auGetLastInvoice(relid,function(data){
                    that.iconLoading = false;
                    if (data && (data.status === 200 || data.status === 201) && data.data){
                        let routeUrl = that.$router.resolve({
                            path: 'order/pay',
                            name: 'payment',
                            params: {
                                isRenew: true,
                            },
                            query: {
                                invoiceId: data.data.id,
                            }
                        });
                        window.open(routeUrl.href, that.$inApp? '_self': '_blank');
                        that.checkRenewStatus()
                    } else {
                        notification['warning']({
                            message: '消息提示',
                            description:
                                '生成續費帳單失敗，請聯繫客服。',
                        });
                    }
                });
            },
            checkRenewStatus() {
                if (this.renewTimer === 0) {
                    this.startPollingTime = new Date().getTime();
                    const that = this;
                    this.renewTimer = setInterval(() => {
                        this.$request.auGetAllServices('','',function (data) {
                            if (data && (data.status === 200 || data.status === 201) && data.data) {
                                localStorage.setItem(AU_EMAIL, data.data.email);
                                localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                                if (data.data.jjService && data.data.jjService.length > 0) {
                                    data.data.jjService.forEach(item => {
                                        let currenId = that.currentService.status === 'Pending' ? that.currentService.id : that.currentService.relid;
                                        if (item.relid === currenId) {
                                            window.clearInterval(that.renewTimer);
                                            that.renewTimer = 0;
                                            localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(item));
                                            that.currentService = item;
                                            that.getServiceInfo();
                                            that.getSettingInfo();
                                        }
                                    });
                                }
                            }
                        });
                        if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 3) {
                            window.clearInterval(that.renewTimer);
                            that.renewTimer = 0;
                        }

                    }, 3000);
                }

            }
        }
    }
</script>

